export default function ({ entities: { Record } }) {
  const state = {
    records: []
  }

  const mutations = {
    add (state, record) {
      state.records.push(record)
    },
    remove (state, recordId) {
      state.records = state.records.filter(record => record.id !== recordId)
    },
    update (state, { id: recordId, data }) {
      // Array.prototype.find() does not works with automatic tests
      const [record] = state.records.filter(record => record.id === recordId)
      record.update(data)
    }
  }

  const actions = {
    addRecord ({ commit }, { name, count, price }) {
      commit('add', new Record(name, count, price))
    },
    removeRecord ({ commit }, recordId) {
      commit('remove', recordId)
    },
    updateRecord ({ commit }, payload) {
      commit('update', payload)
    }
  }

  const getters = {
    totalCount (state) {
      return state.records.reduce((acc, record) => acc + record.count, 0)
    },
    totalPrice (state) {
      return state.records.reduce((acc, record) => acc + (record.count * record.price), 0)
    }
  }

  const store = {
    state,
    mutations,
    actions,
    getters
  }

  return store
}



