<template>
  <li v-if="isEditable"
      class="invoice-editable"
      @keyup.enter="handleEditionConfirm">
    <input class="invoice-name invoice-input"
           v-model="name"
           @focus="$event.target.select()"
           placeholder="Наименование"
           v-focus>
    <input class="invoice-count invoice-input" v-model="count">
    <input class="invoice-price invoice-input" v-model="price">
    <div class="invoice-controls">
      <a class="invoice-button--confirm" href="#" @click.prevent="handleEditionConfirm">
        <i class="fa fa-check" aria-hidden="true"></i>
      </a>
      <a class="invoice-button--cancel" href="#" @click.prevent="handleEditionCancel">
        <i class="fa fa-ban" aria-hidden="true"></i>
      </a>
    </div>
  </li>
  <li class="record" v-else>
    <div class="invoice-name">{{ record.name }}</div>
    <div class="invoice-count">{{ record.count }}</div>
    <div class="invoice-price">{{ record.price }}</div>
    <div class="invoice-controls">
      <a class="invoice-button--edit" href="#" @click.prevent="handleEdit">
        <i class="fa fa-pencil" aria-hidden="true"></i>
      </a>
      <a class="invoice-button--remove" href="#" @click.prevent="handleRemove">
        <i class="fa fa-minus" aria-hidden="true"></i>
      </a>
    </div>
  </li>
</template>

<script>
export default {
  data () {
    return {
      isEditable: true,
      name: '',
      count: 0,
      price: 0
    }
  },
  props: ['record'],
  methods: {
    handleEdit () {
      this.name = this.record.name
      this.count = this.record.count
      this.price = this.record.price
      this.isEditable = true
    },

    handleRemove () {
      this.$emit('remove')
    },

    handleEditionCancel () {
      this.isEditable = false
    },

    handleEditionConfirm () {
      this.$emit('update', {
        name: this.name,
        count: Number(this.count),
        price: Number(this.price)
      })
      this.isEditable = false
    }
  },
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  }
}
</script>

<style>
.invoice-input {
  background-color: whitesmoke;
  border: none;
  border-bottom: 1px solid lightgray;
  font-size: 1rem;
  padding-top: 13px;
  margin-bottom: 9px;
}

.invoice-button--edit {
  color: cornflowerblue;
}

.invoice-button--remove,
.invoice-button--cancel {
  color: crimson;
}

.invoice-button--confirm {
  color: limegreen;
}
</style>



