import nanoid from 'nanoid'

class InvoiceRecord {
  constructor (name = '', count = 1, price = 0) {
    this.id = nanoid()
    this.name = name
    this.count = count
    this.price = price
  }

  update ({ name, count, price }) {
    if (!isEmpty(name)) {
      this.name = name
    }

    if (!isEmpty(count)) {
      this.count = count
    }

    if (!isEmpty(price)) {
      this.price = price
    }
  }
}

export default InvoiceRecord

function isEmpty (x) {
  return x === undefined || x === null || x === ''
}



