<template>
<ul class="invoice">
  <li class="invoice-header">
    <div class="invoice-name">Наименование</div>
    <div class="invoice-count">Количество</div>
    <div class="invoice-price">Цена</div>
    <div class="invoice-controls"></div>
  </li>

  <transition-group name="record-transition">
    <record v-for="record in records"
            class="invoice-record"
            @update="data => updateRecord({ id: record.id, data })"
            @remove="removeRecord(record.id)"
            :key="record.id"
            :record="record"></record>
  </transition-group>

  <li class="invoice-total">
    <div class="invoice-name invoice-name--total">Итого</div>
    <div class="invoice-count invoice-count--total">{{ totalCount }}</div>
    <div class="invoice-price invoice-price--total">{{ totalPrice }}</div>
    <div class="invoice-controls">
      <a href="#" class="invoice-button--add" @click.prevent="addRecord({ name: '', count: 0, price: 0 })">
        <i class="fa fa-plus" aria-hidden="true"></i>
      </a>
    </div>
  </li>
  </records-total>
</ul>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import Record from './components/Record'

export default {
  name: 'app',
  computed: {
    ...mapState([
      'records'
    ]),
    ...mapGetters([
      'totalCount',
      'totalPrice'
    ])
  },
  methods: {
    ...mapActions([
      'addRecord',
      'removeRecord',
      'updateRecord'
    ])
  },
  components: {
    Record
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.invoice {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  margin: auto;
  list-style-type: none;
  width: 1024px;
  border-radius: 1px;
  background-color: whitesmoke;
  padding: 10px 20px;
  box-shadow: 2px 0px 10px 0.1;
}

.invoice-record,
.invoice-total,
.invoice-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.invoice-header {
  text-transform: uppercase;
  font-weight: bold;
}


.invoice-name {
  width: 50%;
}

.invoice-count,
.invoice-price {
  width: 22%;
  text-align: right;
}

.invoice-controls {
  color: lightgray;
  display: flex;
  flex-direction: row;
  font-size: 20px;
  justify-content: space-between;
  line-height: 100%;
  padding-bottom: 25px;
  padding-top: 15px;
  text-align: center;
  width: 60px;
}


.invoice-controls,
.invoice-name,
.invoice-count,
.invoice-price {
  padding: 15px;
  margin: 10px;
}

.invoice-name--total,
.invoice-count--total,
.invoice-price--total {
  font-weight: bold;
}

.invoice-button--add {
  color: cornflowerblue;
}

.record-transition-enter-active,
.record-transition-leave-active {
  transition: all .25s;
}

.record-transition-enter,
.record-transition-leave-to {
  opacity: 0;
}

</style>



